import React, { useState } from 'react';
import './ChannelPartnerLogin.css';

const ChannelPartnerLogin = () => {
  const [email, setEmail] = useState('');
  const [password, setPassword] = useState('');

  const handleLogin = () => {
   
    console.log('Email:', email);
    console.log('Password:', password);
  };

  return (
    <div className="container111">
      <h2 className="header111">Channel Partner Login</h2>
      <div className="input-container111">
        <input
          className="input111"
          type="email"
          placeholder="Email"
          value={email}
          onChange={(e) => setEmail(e.target.value)}
        />
      </div>
      <div className="input-container111">
        <input
          className="input"
          type="password"
          placeholder="Password"
          value={password}
          onChange={(e) => setPassword(e.target.value)}
        />
      </div>
      <button className="login-button111" onClick={handleLogin}>
        Login
      </button>
      <p>
        Don't have an account? <a href="/create-account">Sign Up</a>
      </p>
      <p>
        Forgot your password? <a href="/forgot-password">Forgot Password</a>
      </p>
    </div>
  );
};

export default ChannelPartnerLogin;


