import React, { useState } from 'react';
import './OperationalIn.css';

function OperationalIn() {
  const [asiaPopup, setAsiaPopup] = useState(false);
  const [europePopup, setEuropePopup] = useState(false);
  const [northAmericaPopup, setNorthAmericaPopup] = useState(false);

  const toggleAsiaPopup = () => {
    setAsiaPopup(!asiaPopup);
  };

  const toggleEuropePopup = () => {
    setEuropePopup(!europePopup);
  };

  const toggleNorthAmericaPopup = () => {
    setNorthAmericaPopup(!northAmericaPopup);
  };

  return (
    <div className="operational-in-container">
      <div className="operational-box" onClick={toggleAsiaPopup}>
        Asia Pacific
        {asiaPopup && (
          <div className="popup-menu">
            <ul>
              <li>India</li>
              <li>Singapore</li>
              <li>Thailand</li>
              <li>Australia</li>
              <li>New Zealand</li>
              <li>Hong Kong</li>
              <li>UAE</li>
            </ul>
          </div>
        )}
      </div>
      <div className="operational-box" onClick={toggleEuropePopup}>
        Europe
        {europePopup && (
          <div className="popup-menu">
            <ul>
              <li>United Kingdom</li>
              <li>Ireland</li>
              <li>Netherlands</li>
              <li>Italy</li>
              <li>Hungary</li>
              <li>Switzerland</li>
              <li>Germany</li>
              <li>Poland</li>
              <li>Sweden</li>
              <li>Denmark</li>
              <li>Belgium</li>
              <li>France</li>
              <li>Spain</li>
              <li>Portugal</li>
            </ul>
          </div>
        )}
      </div>
      <div className="operational-box" onClick={toggleNorthAmericaPopup}>
        North America
        {northAmericaPopup && (
          <div className="popup-menu">
            <ul>
              <li>Canada</li>
              <li>United States of America</li>
            </ul>
          </div>
        )}
      </div>
    </div>
  );
}

export default OperationalIn;
